@use "../../scss/abstracts";

.wp-block-md-anitian-fse-full-sample-block {
  background-color: abstracts.$green-theme-color;
  color: abstracts.$white-theme-color;
  padding: 5px 10px;
}

.md_bg_image_upload {
  width: 100%;
  height: 100%;
  border: 1px solid gray;
  justify-content: center;
  align-items: center;
}
.md_bg_image_upload img {
  height: 110px;
  width: 100%;
}
.md_bg_image_upload i {
  width: 100%;
  height: 100%;
  font-size: 100px;
}

.md_anitian_cover_cta_wrap {
  position: relative;
  .md_anitian_cover_cta_arc {
    background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTAwJSIgaGVpZ2h0PSIzMDBweCIgdmlld0JveD0iMCAwIDEyODAgMTQwIiBwcmVzZXJ2ZUFzcGVjdFJhdGlvPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPjxnIGZpbGw9IiNmZmZmZmYiPjxwYXRoIGQ9Ik0wIDE0MGgxMjgwQzU3My4wOCAxNDAgMCAwIDAgMHoiLz48L2c+PC9zdmc+);
    background-size: 100% 300px;
    bottom: 0;
    height: 300px;
    z-index: 1;
    transform: scale(-1, 1);
    position: absolute;
    width: 100%;
  }
  .md_anitian_cover_cta {
    background-position: center;
    background-size: cover;
    background-blend-mode: normal;
    padding: 60px 0;
    .md_anitian_cover_cta__inner {
      z-index: 9;
      position: relative;
      display: flex;
      gap: 7%;
      align-items: center;
      .md_anitian_cover_cta__heading {
        max-width: 51%;
        width: 100%;
        h2 {
          line-height: 1.3em;
          color: #fff;
          margin: 0 0 20px;
          font-weight: 500;
          font-size: 40px;
        }
        p {
          color: #fff;
          margin-bottom: 1em;
        }
        .md_anitian_cover_cta__btn a {
          background-color: #fff;
          color: #1870b8;
          padding: 10px 25px;
          font-size: 14px;
          margin-top: 20px;
        }
      }
      .md_anitian_cover_cta__image {
        max-width: 42%;
        width: 100%;
        .cover_cta__image {
          max-width: 70%;
          width: 100%;
          margin: auto;
          img {
            width: 100%;
            height: auto;
          }
        }
      }
    }
    .background_overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0.8;
      z-index: 1;
      top: 0;
      &::before {
        content: "";
      }
    }
  }

  // Style 2
  &.style2, &.style3 {
    .md_anitian_cover_cta__heading {
      position: relative;
      z-index: 3;
      display: flex;
      flex-direction: column;
      gap: 40px;
      flex-basis: 40%;
      align-items: flex-start;
      text-align: left;
      h2,
      p {
        margin-bottom: 0 !important;
      }
      a {
        background-color: #66c33e !important;
        color: #fff !important;
        font-size: 14px;
        letter-spacing: 0.7px;
        text-transform: uppercase;
      }
    }
    .md_anitian_cover_cta__image {
      flex-basis: 51.3%;
      position: relative;
      z-index: 4;
      height: 100%;
      max-width: 51.3% !important;
      .cover_cta__image {
        max-width: 100% !important;
      }
    }
  }

  // Style 3
  &.style3 {
    .md_anitian_cover_cta__heading {
        gap: 20px;
        h2 {
            color: #fff !important;
            margin: 0 !important;
            font-size: 40px !important;
            line-height: 50px !important;
            font-weight: 500 !important;
        }
        p {
            color: #fff;
            font-size: 20px;
            line-height: 30px;
            font-weight: 500;
            margin: 0;
        }
    }
    .md_anitian_cover_cta__inner {
        padding: 0 0 75px 0;
    }
    .md_anitian_cover_cta__image {
        flex-basis: 51.3%;
        position: relative;
        z-index: 4;
        height: 100%;
      .cover_cta__image {
        max-width: 30% !important;
        margin: 0 0 !important;
        img {
            max-width: 100%;
            border-radius: 15px 0 15px 15px;
        }
      }
    }
  }
}
